body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}
@media (min-width: 900px) {
  .contenedor {
    max-width: 100rem;
  }
}

.bg-custom {
  background-image: url(/build/img/punto.webp);
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
  margin: 0;
}

p {
  margin: 0;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

ul {
  list-style: none;
  padding: 0;
}

.gradiente-letras {
  background-image: linear-gradient(90deg, #1DF1EF, #EF1DF1);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.flecha {
  position: fixed;
  bottom: 1rem;
  right: 1rem;
  width: 4.5rem;
  height: 4.5rem;
  border: 0.2rem solid #0dcecc;
  background-color: rgba(9, 71, 70, 0.6431372549);
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  transition: all 0.2s ease-in-out;
  display: none;
}
.flecha:hover {
  transform: scale(1.1);
}
@media (min-width: 768px) {
  .flecha {
    bottom: 2rem;
    right: 2rem;
  }
}
.flecha i {
  color: #0dcecc;
  font-size: 1.8rem;
}

.no-scroll {
  overflow: hidden;
  height: 100%; /* Esto evitará que la página sea más alta que la ventana del navegador */
}

.ocultar {
  display: none;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.alinear-centro {
  display: flex;
  justify-content: center;
}

.sombra {
  box-shadow: 0.2rem 0.2rem 1rem #959595;
}

.boton-amarillo-block {
  display: block;
  background-color: #E08709;
  font-weight: 700;
  color: #FFFFFF;
  text-align: center;
  padding: 1rem 0;
  margin-top: 2rem;
  cursor: pointer;
}
.boton-amarillo-block:hover {
  background-color: #c77808;
}

.boton-amarillo {
  display: inline-block;
  background-color: #E08709;
  font-weight: 700;
  color: #FFFFFF;
  text-align: center;
  padding: 1rem 0;
  margin-top: 2rem;
  cursor: pointer;
}
.boton-amarillo:hover {
  background-color: #c77808;
}

.boton-verde {
  display: inline-block;
  background-color: #71B100;
  font-weight: 700;
  color: #FFFFFF;
  text-align: center;
  padding: 1rem 0;
  margin-top: 2rem;
  cursor: pointer;
}
.boton-verde:hover {
  background-color: #619800;
}

.boton-rojo {
  display: inline-block;
  background-color: red;
  font-weight: 700;
  color: #FFFFFF;
  text-align: center;
  padding: 1rem 0;
  margin-top: 2rem;
  cursor: pointer;
}
.boton-rojo:hover {
  background-color: #e60000;
}

.ocultar-boton {
  display: none;
}

/** UI **/
header {
  background-color: #094746;
  height: 6rem;
  border-bottom: 0.1rem solid #0c5e5c;
  position: fixed;
  width: 100%;
  z-index: 101;
}
@media (min-width: 768px) {
  header {
    height: 8rem;
  }
}
header .header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
  padding: 0 2rem;
}
@media (min-width: 1260px) {
  header .header {
    padding: 0;
  }
}
header .header .logo {
  width: 13rem;
  min-width: 13rem;
  cursor: pointer;
}
@media (min-width: 768px) {
  header .header .logo {
    width: 17rem;
  }
}
header .header-nav {
  display: flex;
  justify-content: center;
  align-items: center;
}
header .header-nav .navegacion-desktop {
  display: none;
}
@media (min-width: 768px) {
  header .header-nav .navegacion-desktop {
    position: relative;
    display: block;
  }
}
header .header-nav .navegacion-desktop .contenedor-nav {
  display: flex;
}
header .header-nav .navegacion-desktop .contenedor-nav li {
  margin-right: 4rem;
  font-size: 1.6rem;
  font-weight: 700;
}
header .header-nav .navegacion-desktop .contenedor-nav a {
  color: #1DF1EF;
}
header .header-nav .navegacion-desktop .contenedor-nav a:hover {
  color: #0a9e9d;
}
header .header-nav .modos {
  cursor: pointer;
  display: flex;
  align-items: center;
  min-width: 5rem;
  color: #1DF1EF;
}
header .header-nav .modos .icono-sol {
  display: none;
  height: 2.5rem;
  margin: 0 3rem;
}
@media (min-width: 768px) {
  header .header-nav .modos .icono-sol {
    margin: 0;
  }
}
header .header-nav .modos .icono-luna {
  display: block;
  height: 2.5rem;
  margin: 0 3rem;
}
@media (min-width: 768px) {
  header .header-nav .modos .icono-luna {
    margin: 0;
  }
}
header .header-nav .header-mobile {
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (min-width: 768px) {
  header .header-nav .header-mobile {
    display: none;
  }
}
header .header-nav .header-mobile .icono-menu {
  cursor: pointer;
  width: 3rem;
  min-width: 3rem;
  display: flex;
}

.navegacion-mobile {
  z-index: 100;
  display: none;
  width: 100%;
  position: fixed;
  background-color: #094746;
  top: 6rem;
  padding: 1rem 0;
  border-bottom: 0.1rem solid rgba(29, 241, 237, 0.4235294118);
}
.navegacion-mobile.abierto {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
@media (min-width: 768px) {
  .navegacion-mobile.abierto {
    display: none;
  }
}
.navegacion-mobile .contenedor-nav {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
  place-items: center;
  width: 100%;
}
.navegacion-mobile .contenedor-nav li a {
  color: #1DF1EF;
}
.navegacion-mobile .contenedor-nav li a:hover {
  color: #0a9e9d;
}

.contenedor-section-principal {
  background-image: linear-gradient(180deg, #032222, #0a4d4d);
  height: 80vh;
}

.section-principal {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: calc(100% + 8rem);
  padding: 0 1rem;
}
.section-principal .texto-presentacion {
  display: flex;
  flex-direction: column;
  align-items: start;
}
.section-principal .texto-presentacion .saludo {
  color: #FFFFFF;
  font-size: 1.5rem;
  font-weight: 200;
}
.section-principal .texto-presentacion .saludo img {
  display: inline-block;
  width: 2rem;
}
@media (min-width: 480px) {
  .section-principal .texto-presentacion .saludo {
    font-size: 1.7rem;
  }
}
@media (min-width: 768px) {
  .section-principal .texto-presentacion .saludo {
    font-size: 2rem;
  }
}
.section-principal .texto-presentacion h1 {
  font-weight: 800;
  line-height: 1;
  margin: 1rem 0;
  font-size: 2.5rem;
}
@media (min-width: 480px) {
  .section-principal .texto-presentacion h1 {
    font-size: 3.5rem;
  }
}
.section-principal .texto-presentacion .descripcion {
  color: #FFFFFF;
  font-weight: 500;
  font-size: 1.3rem;
  font-weight: 200;
}
@media (min-width: 480px) {
  .section-principal .texto-presentacion .descripcion {
    font-size: 1.7rem;
  }
}
@media (min-width: 768px) {
  .section-principal .texto-presentacion {
    width: 60rem;
  }
}
.section-principal .redes {
  display: flex;
  padding: 0;
}
.section-principal .redes li {
  padding-right: 2rem;
}
.section-principal .redes i {
  font-size: 2rem;
  color: #FFFFFF;
  cursor: pointer;
  transition: transform 0.5s ease-in-out, scale 0.5s ease-in-out;
}
.section-principal .redes i:hover {
  color: #0ab9b6;
  transform: rotate(360deg) scale(1.2);
}

.contenedor-botones .boton {
  width: 9rem;
  font-size: 1rem;
  border: 0.1rem solid #0E9492;
  border-radius: 1rem;
}
@media (min-width: 480px) {
  .contenedor-botones .boton {
    width: 15rem;
    font-size: 1.3rem;
  }
}
@media (min-width: 768px) {
  .contenedor-botones .boton {
    width: 20rem;
    font-size: 1.4rem;
  }
}
.contenedor-botones .cv {
  display: inline-block;
  background-color: #0E9492;
  font-weight: 700;
  color: #FFFFFF;
  text-align: center;
  padding: 1rem 0;
  margin-top: 2rem;
  cursor: pointer;
  margin-right: 1rem;
  transition: box-shadow 0.2s ease-in;
}
.contenedor-botones .cv:hover {
  background-color: #0c7d7b;
}
.contenedor-botones .cv:hover {
  box-shadow: 0 0 0.7rem 0.1rem rgba(14, 148, 146, 0.3843137255);
}
.contenedor-botones .proyectos {
  display: inline-block;
  background-color: rgba(0, 0, 0, 0);
  font-weight: 700;
  color: #FFFFFF;
  text-align: center;
  padding: 1rem 0;
  margin-top: 2rem;
  cursor: pointer;
}
.contenedor-botones .proyectos:hover {
  background-color: rgba(0, 0, 0, 0);
}
.contenedor-botones .proyectos:hover {
  color: #1DF1EF;
  box-shadow: 0 0 0.7rem 0.1rem rgba(14, 148, 146, 0.2823529412);
}

.contenedor-section-aptitudes {
  background-color: #0bb6b5;
  width: 100%;
}
.contenedor-section-aptitudes .contenedor-aptitudes {
  padding: 5rem 0;
}
.contenedor-section-aptitudes .contenedor-aptitudes ul {
  display: grid;
  gap: 4rem;
  grid-template-columns: repeat(1, 1fr);
  padding: 0 2rem;
  margin: 0;
}
@media (min-width: 480px) {
  .contenedor-section-aptitudes .contenedor-aptitudes ul {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 900px) {
  .contenedor-section-aptitudes .contenedor-aptitudes ul {
    display: grid;
    gap: 3rem;
