.contenedor-section-aptitudes .contenedor-arriba .contenedor-icono img {
  width: 2.5rem;
  height: 2.5rem;
}
.contenedor-section-aptitudes .contenedor-arriba p {
  font-weight: 700;
}
.contenedor-section-aptitudes .aptitud-descripcion {
  padding-top: 1rem;
  font-weight: 100;
  font-size: 1.3rem;
}

